body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #324D58 url(/static/media/weather.12e5c2fe.jpg) no-repeat center center fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  width: 70%;
  margin: 0px auto;
}

.weather-header h1 {
  color: white;
}

.weather-header h1 span {
  background-color: rgba(0,0,0,.4);
  padding: 0px 5px;
  box-shadow: 2px 2px 30px rgba(0,0,0,.6);
}

.search-box {
  padding: 30px 0px 40px 0px;
}

.search-box p {
  color: white;
  font-size: 20px;
}

.search-box input {
  border: 1px solid #000006;
  outline: none;
  caret-color: white;
  background-color: rgba(0,0,0,.4);
  padding: 8px 10px;
  margin-right: 10px;
  margin-bottom: 20px;
  border-radius: 4px;
  color: white;
}

.search-box button {
  display: block;
  outline: none;
  background-color: #000006;
  border: none;
  padding: 8px 12px;
  color: white;
  cursor: pointer;
  border-radius: 4px;
  margin-top: 20px;
  box-shadow: 0px 0px 20px rgba(0,0,0,.4);
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

.search-box button:hover {
  box-shadow: 0px 5px 20px rgba(0,0,0,.6);
}

.search-box button::-moz-focus-inner {
  outline: none;
  border: 0;
}

.weather-result {
  /* background-color: rgba(0,0,0,.4);
  padding: 20px 30px; */
  color: white;
}

.weather-result p {
  font-size: 20px;
  line-height: 20px;
}

.weather-result p span {
  color: #94cf65;
}
